@import "./globales.scss";
@media only screen and (max-width: 840px) {
    header {
        overflow-y: hidden;
    }
    .body.active {
        overflow: hidden;
    }
    .menu-button {
        width: 35px;
        height: 30px;
        margin-right: 15px;
        display: block;
        position: fixed;
        z-index: 1;
    }
    .hamburger {
        display: block;
        cursor: pointer;
        z-index: 2;
        margin-right: 10px;
    }
    .hamburger.active {
        position: fixed;
        z-index: 999;
    }
    .bar {
        background-color: #000000;
    }
    .nav-menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        background-color: rgb(0, 0, 0);
        width: 100%;
        height: 100%;
        right: -100%;
        top: 0;
        transition: 0.5s;
        margin-right: 0;
        overflow-y: unset;
        z-index: 99;
    }
    .avatar-menu {
        position: absolute;
        right: 0;
        bottom: 0;
        display: block;
    }

    .nav-item {
        margin: 25px 0;
    }
    .nav-link {
        color: white;
        font-family: $font-text;
        font-weight: 300;
        font-size: 25px;
        margin: 25px 0;
    }
    .nav-menu.active {
        right: 0;
    }
    /* Main One */
    .main_one {
        padding-top: 4vh;
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-bottom: 0px;
    }

    
    .main_one-text {
        display: flex;
        max-width: 670px;
        max-height: 380px;
        padding: 48px 18px;
        flex-direction: column;
        align-items: flex-start;
        gap: 30px;
        margin: 0px 12px;
    
        border-radius: 16px;
        background: linear-gradient(138deg, rgba(255, 255, 255, 0.70) 0%, rgba(255, 255, 255, 0.70) 100%);
        backdrop-filter: blur(67.5px);
    }

    .main_one-text > h1 {
        text-align: left;
        font-size: 28px;
    }

    .main_one .main_one-text > p {
        font-family: $font-text;
        text-align: center;
    }
    .main_one .main_one-text > .main_on-presentation {
        font-family: $font-text;
        text-align: left;
    }
    .main_one .main_one-text > p > span {
        font-family: $font-title;
        font-weight: bold;
    }
    .main_one-profile{
        margin: 0px 12px;
        width: -webkit-fill-available;
        width: -moz-available;
    }

    .main_one-button {
        color: white;
        background-color: black;
        text-decoration: none;
        margin: 40px 0 10px 0;
        padding: 10px;
        font-family: $font-text;
        font-style: normal;
        font-weight: 300;
        font-size: 20px;
        line-height: 30px;
        border: 1px solid #040404;
        box-sizing: border-box;
        border-radius: 14px;
    }
    .main_one-button:active {
        color: black;
        background-color: white;
        text-decoration: none;
        margin: 40px 0 10px 0;
        padding: 10px;
        font-family: $font-text;
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 30px;
        border: 1px solid #040404;
        box-sizing: border-box;
        border-radius: 14px;
    }
    .About-Main {
        height: auto;
    }
    .about_section{
        display: flex;
        flex-direction: column;
        padding: 0px 12px;
    }
    .about_section--info {
        align-items: center;
        margin: 10px 0px;
    }

    .about_section--info p{
        text-align: center;
    }

    .SectionThree{
        margin: 30px 16px;
        .SectionThree-tittle{
            font-size: 22px;
        }
        .SectionThree-text{
            text-align: left;
        }
    }

}
@media only screen and (min-width: 570px) {

