@import "./globales.scss";

/* .menu-button {
    display: none;
} */

.hamburger {
    display: none;
}
.avatar-menu {
    display: none;
}

.bar {
    display: block;
    width: 25px;
    height: 3px;
    margin: 5px auto;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    background-color: #000000;
}

/* Mobile Menu */
.nav-menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right: 30px;
}

/* This ðŸ‘‡ */
.main_one {
    width: 100%;
    height: 70vh;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    margin-bottom: -30px;
}

/* This ðŸ‘‡ holi */
.main_one-text {
    display: flex;
    max-width: 670px;
    max-height: 380px;
    padding: 48px 24px;
    flex-direction: column;
    align-items: flex-start;
    gap: 30px;

    border-radius: 16px;
    background: linear-gradient(138deg, rgba(255, 255, 255, 0.70) 0%, rgba(255, 255, 255, 0.70) 100%);
    backdrop-filter: blur(67.5px);
}
.main_one .main_one-text > p {
    font-family: $font-text;
    text-align: initial;
}
.main_one .main_one-text > p > span {
    font-family: $font-title;
    font-weight: bold;
}

.main_one-text a{
    color: #000;
    font-size: 16px;
    display: flex;
    padding: 10px;
    justify-content: center;
    align-items: center;
    gap: 4px;
    border: 0.5px solid;
    border-radius: 8px;
    background: #9ADECE;
    margin: 0px;
}

.main_one-text > h1{
    margin-bottom: 0px;
}
.main_on-subtittle{
    font-size: 15px;
    font-weight: normal;
    text-align: left;
}

.main_on-presentation{
    font-size: 18px;
    margin-top: 14px;
}

.main_one-profile{
    display: flex;
    //width: 380px;
    max-height: 380px;
    padding: 35px 24px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    flex-shrink: 0;

    border-radius: 16px;
    background: linear-gradient(138deg, #EFEFEF 0%, rgba(239, 239, 239, 0.50) 100%);
    backdrop-filter: blur(20px);
}


.main_one-profile img{
    width: 200px;
    height: 200px;
}

.about_section{
    display: flex;
    justify-content: center;
    margin: 0px 0px 30px;
}
.about_section--info{
    max-width: 550px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;

    p{
        text-align: left;
    }
}

.main-two{
    display: flex;
    justify-content: center;
    margin: 0px 0px 30px;
}

.footer {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.main_one-profile_social-networks {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.proyectListHome_item{
