.SectionThree{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: flex-start;
    max-width: 1067px;
    margin: 30px auto;

    .SectionThree-tittle{
        text-align: left;
    }
    .SectionThree-mail{
        display: flex;
        gap: 8px;
        margin-top: 18px;

        .SectionThree-mail-container{
            text-align: left;

            :first-child{
                font-weight: bold;
                font-size: 20px;
            }
            :last-child{
                font-size: 18px;
                margin: 0;
            }
        }
    }
}