@media only screen and (max-width: 840px) {
  header {
    overflow-y: hidden;
  }
  .body.active {
    overflow: hidden;
  }
  .menu-button {
    width: 35px;
    height: 30px;
    margin-right: 15px;
    display: block;
    position: fixed;
    z-index: 1;
  }
  .hamburger {
    display: block;
    cursor: pointer;
    z-index: 2;
    margin-right: 10px;
  }
  .hamburger.active {
    position: fixed;
    z-index: 999;
  }
  .bar {
    background-color: #000000;
  }
  .nav-menu {
    position: fixed;
    flex-direction: column;
    justify-content: center;
