    width: 100%;
    height: 100%;
    right: -100%;
    top: 0;
    transition: 0.5s;
    margin-right: 0;
    overflow-y: unset;
    z-index: 99;
  }
  .avatar-menu {
    position: absolute;
    right: 0;
    bottom: 0;
    display: block;
  }
  .nav-item {
    margin: 25px 0;
  }
  .nav-link {
    color: white;
    font-family: "Quicksand";
    font-weight: 300;
    font-size: 25px;
    margin: 25px 0;
  }
  .nav-menu.active {
    right: 0;
  }
