* {
  margin: 0;
  padding: 0;
}

header {
  width: 100%;
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-family: "Quicksand";
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  font-size: 1.5rem;
  margin: 0 8px 0;
  color: black;
  font-weight: 300;
}

a:hover {
  border-bottom: rgb(17, 17, 17) solid 1px;
}

h1,
h2,
a {
  font-family: "Montserrat";
}

h1 {
  margin: 10px 0;
  font-size: 40px;
}

body {
  max-width: 1350px;
  margin: auto;
}

p {
  font-family: "Quicksand";
}

header {
  z-index: 9999;
}

.About-Main {
  height: 80vh;
}

@media only screen and (max-width: 840px) {
  .About-Main {
    height: auto;
  }
}/*# sourceMappingURL=About.css.map */