.proyectList {
  margin: 50px 0 0 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 12px;
}

.proyectList_links {
  background-color: black;
  color: white;
  text-decoration: none;
  padding: 3px;
  border: 0.5px solid white;
}

.proyectList_links:hover {
  background-color: pink;
  color: black;
}

.proyectList_item {
  margin: 50px 55px 50px;
  border: 1px solid black;
  padding: 10px 0 10px;
}

.proyectList_item:hover {
  background-color: black;
  color: white;
}

.proyectList_links-code {
  display: flex;
  padding: 10px;
  justify-content: center;
  align-items: center;
  gap: 4px;
  border-radius: 8px;
  border: 1px solid #9ADECE;
  font-size: 20px;
  margin: 0;
}
.proyectList_links-code .github_logo {
  width: 24px;
  height: 24px;
  content: url("../components/img/mdi_github.svg");
}

.proyectList_links-code:hover {
  border-radius: 8px;
  background: #0F0F0F;
  color: white;
}
.proyectList_links-code:hover .github_logo {
  width: 24px;
  height: 24px;
  content: url("../components/img/mdi_github-white.svg");
}

.proyectList_links-demo {
  display: flex;
  padding: 10px;
  justify-content: center;
  align-items: center;
  gap: 4px;
  border-radius: 8px;
  background: #9ADECE;
  font-size: 20px;
  margin: 0;
}
.proyectList_links-demo .world_logo {
  width: 24px;
  height: 24px;
  content: url("../components/img/jam_world.svg");
}

.proyectList_links-demo:hover {
  border-radius: 8px;
  background: #0F0F0F;
  color: white;
}
.proyectList_links-demo:hover .world_logo {
  width: 24px;
  height: 24px;
  content: url("../components/img/jam_world-white.svg");
}

.proyectList_links-block {
  display: flex;
  gap: 8px;
}

.proyectListHome_item {
  display: flex;
  width: 300px;
  padding: 25px;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 14px;
  flex-shrink: 0;
  border-radius: 16px;
  background: linear-gradient(138deg, rgba(255, 255, 255, 0.7) 0%, rgba(255, 255, 255, 0.7) 100%);
  box-shadow: 5px 4px 27px 0px rgba(103, 100, 100, 0.25);
  -webkit-backdrop-filter: blur(67.5px);
          backdrop-filter: blur(67.5px);
}
.proyectListHome_item p, .proyectListHome_item h2 {
  text-align: left;
}
.proyectListHome_item img {
  width: -webkit-fill-available;
  width: -moz-available;
  width: 100%;
  height: 140px;
  border-radius: 4px;
}
.proyectListHome_item .proyectList_tech-stack {
  width: 23px;
  height: 23px;
  margin-right: 4px;
}

.proyectListHome_item-tech_stack {
  display: flex;
  gap: 12px;
}
.proyectListHome_item-tech_stack p {
  font-weight: bold;
}/*# sourceMappingURL=Projects.css.map */